@import "./../../variables";

.main-screen {
 height: 100%;
 display: flex;
 background: #F7F7F7;
 padding: 0 30px 44px;
 box-sizing: border-box;

 .screen-wrapper {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
 }

 .logo-wrapper {
  height: 90px;
  display: flex;

  .logo-ranepa {
   margin: auto auto auto 0;
   padding-top: 5px;
   width: 161px;
  }
 }

 .work-wrapper {
  flex: 1 1 auto;
  background: white;
  border-radius: 20px;
  display: flex;
  padding: 40px;
  overflow: auto;
 }

 .top-panel {
  min-height: 90px;
  display: flex;
  justify-content: space-between;

  .links {
   display: flex;

   a {

    color: #686868;
    text-decoration: none;
    text-transform: uppercase;
    margin: auto 40px auto 0;

    &:last-of-type {
     margin-right: 0;
    }

    &.--active {
     font-weight: 500;
     color: #A30236;
    }
   }
  }
 }

 .side-panel {
  margin-right: 30px;
 }

 .user-data {
  display: flex;
  align-items: center;

  .notification {
   margin-right: 56px;
  }

  .user {
   margin-right: 16px;
   .name {
    font-weight: 500;
    font-size: 16px;
   }
   .role {
    font-weight: 400;
    font-size: 12px;
   }
  }

  .user-photo {
   margin: auto;
  }
 }


}