.slidecontainer {
  width: 100%;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  border-radius: 3px;
  height: 5px;
  outline: none;
  background: linear-gradient(90deg, #FF0000 15.9%, #00FF57 100%);
  -webkit-transition: .2s;
  transition: all .2s;
}

//.slider:hover {
//  opacity: 1;
//}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: url("./../sources/svg/sliderArrows.svg"), linear-gradient(95.69deg, #A30236 3.42%, #D90348 93.2%);
  background-size: cover;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: url("./../sources/svg/sliderArrows.svg"), linear-gradient(95.69deg, #A30236 3.42%, #D90348 93.2%);
  background-size: cover;
  cursor: pointer;
}







.menu-button {
  text-decoration: none;
  text-transform: uppercase;
  background: linear-gradient(95.69deg, #A30236 3.42%, #D90348 93.2%);
  border-radius: 10px;
  font-size: 18px;
  color: white;
  font-weight: 500;
  max-height: 218px;
  flex: 1 1 0;

  padding-top: 48px;
  padding-bottom: 40px;

  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;

  margin-right: 18px;
  &:last-of-type {
    margin-right: 0;
  }
}